import { h } from 'preact'
import { useEffect } from 'preact/hooks'

import Hero from './../../components/Hero/Hero'
import How from './../../img/how.png'
import Apps from './../../img/apps.png'
import { route, root, center, singleCenter, fullWidth } from './Home.module.scss'


export default function Home() {

	useEffect(() => {
		window.scrollTo(0, 0)
	}, [])

	return (
		<div className={[route, root].join(' ')}>

			<Hero />

			<section className={singleCenter}>
				<a name="why"></a>
				<div style={{padding: 16, border: '2px solid #ddd'}}>
				<h1>Why Hashcache?</h1>
				<p>You like designing, developing and delivering amazing products.</p>
				<p>You <em>don't</em> like worrying about security, privacy or compliance.</p>
				<p>Hashcache will:</p>
				<ul>
					<li>Reduce risk of data breach</li>
					<li>Reduce compliance burden</li>
					<li>Eliminate security, development and legal costs</li>
				</ul>
				</div>
			</section>
			
			<section>
				<h1>Problem</h1>

				<ul>
					<li>You store customer data in the cloud so it can be accessed from any device</li>
					<li>Customers are increasingly concerned about their data</li>
					<li>Successful breaches can encrypt or extract <em>all</em> data</li>
				</ul>
			</section>
			<section>
				<h1>Solution</h1>
				<ul>
					<li>Customers secure sensitive data before uploading to the cloud</li>
					<li>A breach of your database does not reveal any sensitive customer data</li>
					<li>You reduce the amount of data you need to secure and make compliant</li>
				</ul>
			</section>

			<section className={singleCenter}>
				<a name="how"></a>
				<h1>How it Works</h1>
				<p>Alice (A) and Bob (B) want to use your messaging feature to discuss some confidential business.</p>
				<img src={How} alt="how it works" />
			</section>
			
			<section className={fullWidth}>
				
				<section className={fullWidth}>
					<a name="apps"></a>
					<h1>Applications</h1>
				</section>

				<div className={center}>
					<h3>Messaging</h3>
					<i class="far fa-comments fa-4x"></i>
				</div>
				<div className={center}>
					<h3>Fintech</h3>
					<i className="fas fa-file-invoice-dollar fa-4x"></i>
				</div>
				<div className={center}>
					<h3>Healthcare</h3>
					<i className="fas fa-notes-medical fa-4x"></i>
				</div>
				
			</section>
			
			<section className={singleCenter}>
				<h1>Example: Spreadsheets</h1>
				<ol>
					<li>Customers want their sensitive data kept secure and private</li>
					<li>You want app usage metrics such as: files created, daily active, AB feature tests, ...</li>
				</ol>
				<img src={Apps} alt="applications" />
				<p><strong>Solution</strong></p>
				<p>Use Hashcache to secure <em>only</em> the cells of spreadsheets containing sensitive raw data before uploading to the cloud.</p>
				<p>Your app can still leverage metadata such as: rows, columns and filenames to provide analytics and support.</p>
				<p><strong>Key Points</strong></p>
				<ul>
					<li>The customer is now in control of securing their sensitive data</li>
					<li>You retain quality app usage metrics and Analytics</li>
					<li>With <em>less</em> sensitive data stored in your database, you can save on security and compliance costs</li>
				</ul>
				<p><strong>A win win for you and your customers</strong></p>
			</section>


			
			<section>
				<a name="about"></a>
				<h1>About</h1>
				<p>
					Hashcache is developed in Vancouver, B.C. by <a href="https://twitter.com/mattdlockyer">Matt Lockyer</a>.
				</p>
			</section>


			<section>
				<a name="contact"></a>
				<h1>Contact</h1>
				<p><a href="mailto:hashcacheio@gmail.com">hashcacheio@gmail.com</a></p>
			</section>
			

		</div>
	)
}