import { h } from 'preact'
import { useEffect } from 'preact/hooks'

import encrypt from './encrypt.svg'
import decrypt from './decrypt.svg'

import { route, root, col2, code, text } from './How.module.scss'

const rc = (a) => <span className={code}>{a}</span>

export default function How() {

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <div className={[route, root].join(' ')}>
      
      <section>
        <h1>Why Hashcache?</h1>
        <p>
          Our mission is making encrytion simple and easy for any developer. We believe the first step to privacy preserving applications puts the user in control of their data.  
        </p>
      </section>
      
      <section>
        <h3>How it Works - Encryption</h3>
        <div className={col2}>
          <div>
            <img src={encrypt} />
          </div>
          <div className={text}>
            <ol>
              <li>Alice is using MyApp</li>
              <li>Alice opens/resumes {rc('hc.session')}</li>
              <li>Alice creates {rc('data')} and an {rc('hc.key')} </li>
              <li>MyApp calls {rc('hc.encrypt')}</li>
              <li>{rc('data')} is now {rc('encrypted')}</li>
            </ol>
            <p>
              An {rc('hc.session')} is created when a unique user of MyApp provides a password. We call this  <b>"Data 2 Factor Authentication" (D2FA)</b>.
            </p>
            <p>
              The {rc('hc.session')} has a unique {rc('session key')} used to encrypt all key material.
            </p>
            <p>
              Each piece of data is encrypted with a unique one time use "data key".
            </p>
          </div>
        </div>
      </section>

      <section>
        <h3>How it Works - Decryption</h3>
        <div className={col2}>
          <div>
            <img src={decrypt} />
          </div>
          <div className={text}>
            <p>
              MyApp has fetched Alice's encrypted data and the Hashcache session will provide a corresponding data key.
            </p>
            <ol>
              <li>MyApp provides {rc('encrypted')} data</li>
              <li>Alice provides the {rc('hc.key')}</li>
              <li>MyApp calls {rc('hc.decrypt')}</li>
              <li>{rc('encrypted')} is now decrypted {rc('data')}</li>
            </ol>
            <p>
              The {rc('hc.session')} has a unique {rc('session key')} used to encrypt and decrypt all subsequent keys and data.
            </p>
          </div>
        </div>
      </section>
      
      <section>
        <h1>Build Apps Differently</h1>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
        </p>
      </section>
    </div>
  )
}